@use '@mview/scss/utilities/functions' as *;
@use '@mview/scss/utilities/mixins' as *;
@use 'sass:color';

.edit-layout-wrapper {
  .title-info {
    svg {
      fill: pick-color('common', 'warning');
      width: 20px;
      height: 20px;
      cursor: help;
    }
  }

  .output-dropdown-wrapper {
    max-width: 160px;
  }

  .edit-layout-state {
    .redo {
      svg {
        transform: rotateY(180deg);
      }
    }

    button {
      line-height: 18px;
      border: none;

      span {
        height: 17px;
      }
    }
  }

  .el-file-error-ph {
    display: flex;
    flex-direction: column;
    justify-items: center;
    align-items: center;

    mat-icon {
      font-size: 32px;
      width: 32px;
      height: 32px;
    }
  }

  .edit-layout-loader {
    width: 100%;
    overflow: hidden;
    overflow-x: scroll;
    $bg: color.adjust(pick-color('secondary', 'blue'), $lightness: 30%);
    $fg: color.adjust(pick-color('secondary', 'blue'), $lightness: 20%);

    background-color: color.adjust(pick-color('secondary', 'blue'), $lightness: 38%);
    padding: 16px 20px;
    border-radius: 8px;

    .frames-wrapper {
      display: flex;
      align-items: center;
      height: 54px;
      gap: 3px;

      .frame {
        width: 98px;
        flex-shrink: 0;
        height: 100%;
        border-radius: 6px;
      }
    }

    .waveform {
      width: 400px;
    }
  }

  .edit-layout-trackpad {
    overflow: hidden;
    overflow-x: scroll;
    background-color: color.adjust(pick-color('secondary', 'blue'), $lightness: 38%);
    padding: 16px 20px;
    border-radius: 8px;
    position: relative;

    &::-webkit-scrollbar {
      height: 6px;
    }

    &::-webkit-scrollbar-thumb {
      background-color: pick-color('primary', 'fog');
    }

    .trackpad-cursor {
      height: 100%;
      top: 1px;
      width: 20px;
      left: 10px;
      z-index: 50;
      cursor: grab;
      position: absolute;

      .trackpad-cursor-inside {
        display: flex;
        flex-direction: column;
        align-items: center;

        .cursor-stick {
          width: 8px;
          height: 16px;
          background-color: pick-color('common', 'danger');
          // background-color: pick-color('primary', 'web');
          border-bottom-right-radius: 10px;
          border-bottom-left-radius: 10px;
        }

        .cursor-line {
          width: 2px;
          flex-grow: 1;
          background-color: pick-color('common', 'danger');
          // background-color: color.adjust(pick-color('primary', 'web'), $lightness: 0%);
          margin-top: -2px;
        }
      }
    }

    .trackpad-ruler {
      width: 400px;
      user-select: none;

      .ruler-division {
        display: flex;
        align-items: center;

        .ruler-label {
          flex-shrink: 0;
          width: 20px;
          padding: 0 3px;
          font-size: 10px;
          color: pick-color('primary', 'charcoal');
          font-weight: 500;
          transform: translate(-60%);
        }

        .ruler-point {
          flex-shrink: 0;
          width: 20px;
          padding: 0 3px;
        }
      }
    }

    .trackpad-frames-container {
      width: 400px;

      .trackpad-frames-inside {
        margin-top: 12px;
        padding-right: 20px;
        position: relative;

        .track-element__placeholder {
          // position: absolute;
          height: 100%;
          padding: 0 2px;
          transform: translate3d(0px, 0px, 0px);
          z-index: 30;
          top: 0;
          flex-shrink: 0;

          &.element__placeholder-left {
            left: 0;
          }

          &.element__placeholder-right {
            right: 0;
            padding-left: 2px;
          }

          .track-element__placeholder-background {
            height: 54px;
            border-radius: 4px;
            background-color: #27272c;
            cursor: pointer;
            transition: background-color 0.1s ease-out;
          }
        }

        .frames-edit-options {
          flex-shrink: 0;
          width: 100%;
          height: 54px;
          position: absolute;

          .track-element__border {
            height: 100%;
            // border: 2px solid pick-color('common', 'warning');
            border: 2px solid pick-color('secondary', 'blue');
            border-radius: 4px;
            background-color: transparent;
            position: absolute;
            z-index: 40;
            top: 0;
            bottom: 0;
            left: 0;
            right: 16px;
          }

          .track-element__stick-layout {
            position: absolute;
            z-index: 40;
            height: 100%;
            top: 0;
            cursor: ew-resize;

            &.track-element__stick-layout-left {
              left: 0;
            }

            &.track-element__stick-layout-right {
              right: 0;
              padding-right: 20px;
            }

            .track-element__stick {
              height: 100%;
              // background-color: pick-color('common', 'warning');
              background-color: pick-color('secondary', 'blue');
              width: 10px;
              display: flex;
              align-items: center;
              justify-content: center;

              &.track-element__stick-left {
                border-radius: 8px 0 0 8px;
              }

              &.track-element__stick-right {
                border-radius: 0 8px 8px 0;
              }

              &::before {
                width: 2px;
                content: '';
                display: block;
                height: 33%;
                min-height: 12px;
                background: rgba(0, 0, 0, 0.2);
                border-radius: 2px;
              }
            }
          }
        }

        .video-frames-wrapper {
          width: 100%;
          height: 54px;
          overflow: hidden;

          .video-frame {
            width: 98px;
            flex-shrink: 0;
            height: 100%;
            background-size: auto 54px;
            background-repeat: repeat-x;
            background-position: left center;
            z-index: 21;
            border-radius: 6px;

            &:not(:last-child) {
              margin-right: 2px;
            }
          }
        }

        .audio-frames-wrapper {
          user-select: none;
          -webkit-user-select: none;
          -moz-user-select: none;

          .canvas-wrapper {
            position: relative;

            canvas {
              display: block;
              height: 0;
            }

            .beep-intervals {
              position: absolute;
              height: 100%;
              width: 100%;
              top: 0;
              left: 0;

              .interval {
                z-index: 60;
                position: absolute;
                left: 0;
                top: 0;
                height: 100%;
                width: 0;
                background-color: rgba(pick-color('secondary', 'green'), 0.5);

                .interval-inside {
                  position: relative;
                  height: 100%;

                  .matte-bg {
                    background-color: rgba($color: #000000, $alpha: 0.3);
                    height: 100%;
                    width: 100%;
                    opacity: 0;
                    transition: opacity 0.2s;

                    .interval-time {
                      background-color: white;
                      border: 2px solid rgba(pick-color('secondary', 'blue'), $alpha: 0.5);
                      color: pick-color('secondary', 'blue');
                      padding: 2px 8px;
                      position: absolute;
                      top: 31%;
                      left: 50%;
                      transform: translate(-50%, -100%);
                      font-size: 12px;
                      border-radius: 8px;
                      white-space: nowrap;
                      font-weight: 500;
                      opacity: 0;
                      transition: opacity 0.2s;

                      &::after {
                        content: '';
                        width: 10px;
                        height: 6px;
                        position: absolute;
                        bottom: -55%;
                        left: 50%;
                        transform: translate(-50%);
                        // background-color: white;
                        // border-bottom-right-radius: 100px;
                        // border-bottom: 2px solid rgba(pick-color('secondary', 'blue'), $alpha: 0.5);
                        // background: linear-gradient(to bottom right, #fff 0%, #fff 50%, green 50%, green 100%);
                        width: 0px;
                        height: 0px;
                        border: 6px solid transparent;
                        border-top-color: white;
                      }
                    }

                    .interval-delete {
                      padding: 2px;
                      border: 2px solid rgba(pick-color('secondary', 'blue'), $alpha: 0.5);
                      border-radius: 50px;
                      background-color: white;
                      display: flex;
                      align-items: center;
                      justify-content: center;
                      opacity: 0;
                      transition: opacity 0.2s;
                      cursor: pointer;

                      svg {
                        width: 20px;
                        height: 20px;
                      }
                    }
                  }

                  &:hover {
                    .matte-bg,
                    .matte-bg > .interval-time,
                    .matte-bg > .interval-delete {
                      opacity: 1;
                    }
                  }
                }
              }
            }

            .audio-clip-wrapper {
              position: absolute;
              background-color: rgba(pick-color('secondary', 'blue'), $alpha: 0.2);
              height: 100%;
              top: 0;
              display: flex;
              align-items: center;
              justify-content: space-between;
              border-radius: 8px;

              .left-handle {
                width: 8px;
                height: 100%;
                border-left: 6px solid pick-color('primary', 'web');
                border-top-left-radius: 50px;
                border-bottom-left-radius: 50px;
              }

              .right-handle {
                width: 8px;
                height: 100%;
                border-right: 6px solid pick-color('primary', 'web');
                border-top-right-radius: 50px;
                border-bottom-right-radius: 50px;
              }
            }
          }
        }
      }
    }
  }

  .edit-options {
    // display: grid;
    // grid-template-columns: repeat(5, 1fr);
    display: flex;
    align-items: center;
    gap: 8px;

    button {
      border: 1px solid pick-color('primary', 'blue');
      color: pick-color('primary', 'blue');
      font-size: 14px;
      font-weight: 400;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-grow: 1;

      svg {
        width: 22px;
        height: 22px;
        font-size: 22px;
        margin-right: 6px;
        fill: pick-color('primary', 'blue');
        stroke: pick-color('primary', 'blue');
      }

      &.activated {
        background-color: pick-color('primary', 'blue');
        color: white;

        svg {
          fill: white;
          stroke: white;
        }
      }
    }
  }
}
